@import "../../node_modules/bootstrap/scss/bootstrap";
// Update the import directory to point to its location within node_modules
$bootstrap-icons-font-dir: "../../node_modules/bootstrap-icons/font/fonts";

// Import the Sass files as usual
@import "../../node_modules/bootstrap-icons/font/bootstrap-icons";

body {
  font-family: "vazir", "IRANSansX", sans-serif; /*  \u200c     style.css       */
  color: #333;
  direction: rtl; /*   \u200c RTL */
}

.navbar-brand img {
  max-height: 80px; /*   */
}
.hero-section {
  position: relative;
  height: 80vh; /*    */
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
}
.hero-video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -1;
  transform: translate(-50%, -50%);
  background-size: cover;
}
.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6); /*     */
  z-index: 0;
}
.hero-content {
  position: relative;
  z-index: 1;
  padding: 20px;
}
.section-title {
  text-align: center;
  margin-bottom: 40px;
  font-weight: bold;
  color: #1a73e8; /*    */
}
.card {
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
  border: none; /*     \u200c */
}
.card:hover {
  transform: translateY(-5px);
}
.card-img-top {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  height: 200px; /*       */
  object-fit: cover; /*    */
}
.footer {
  background-color: #f8f9fa; /*      */
  color: #333;
  padding: 40px 0;
  border-top: 1px solid #eee;
}
.social-icons a {
  color: #1a73e8;
  font-size: 24px;
  margin: 0 10px;
  transition: color 0.2s;
}
.social-icons a:hover {
  color: #0d47a1;
}
